import Cards from "./Cards";
import contentpt from "@/assets/contentpt.json";
import contenten from "@/assets/contenten.json";
import { useLanguage } from "../header/LanguageContext";

function createCards(card, index) {
  return (
    <Cards key={index} icon={card.icon} title={card.title} text={card.subTitle} />
  );
}

function IconCardsInfo() {
  const { language } = useLanguage();
  const content = language === "pt" ? contentpt : contenten;

  // Seleciona os cards do JSON
  const cardsData = content.homeCards.text;

  return (
    <div className="homeCover">
      <div className="frame-cards">
        <h1>{content.cardsTitle || "Destaques"}</h1>
        <div className="cardsFrame">{cardsData.map(createCards)}</div>
      </div>
    </div>
  );
}

export default IconCardsInfo;
